ul{
  list-style: none;
  padding-left: 0;
}

a{
  text-decoration: none;
}

.pg--header{
  padding: 0;
}

.pg--header ul  li{
  padding: 1.5rem 0;
  line-height: 1;
}

.pg--header ul  .nav-link{
  color: #fffc !important;
}


.pg--header ul  li:not(:last-child)  a{
  margin: 0 1.5rem;
  position: relative;
  letter-spacing: 1px;
  font-size: 14px;
  z-index: 1;
}

.pg--header ul  li:last-child a{
  margin-left: 2rem;
  transform: scale(1.2);
}

.pg--header ul  li:not(:last-child) > a:before{
  content: '';
  position: absolute;
  width:30px;
  height: 1px;
  background-color:red;
  top: 25%;
  left: 50%;
  transform: rotate(-45deg) translate(-50%,-50%);
  opacity: 0;
  z-index: -1;
}

.pg--header ul  li:not(:last-child):hover > a:before{
  opacity: 1;
}

.pg--header ul > li > a:hover{
    color: #fff;
}






.pg--hero{
  height: 100vh;
}













.pg--header,
.pg--footer{
  background-color: #000 !important;
}

.pg--footer {
  color: #fff5;
  border-top: 1px solid #fff;
}


.pg--footer ul.bt-link{
  display: flex;
  justify-content: flex-end;
  color: #fffa;
}

.pg--footer ul.bt-link li:not(:first-child){
  margin-left: 2rem;
}

.pg--Ftlink{
  display: flex;
  justify-content: center;
  height: 200px;
}

.pg--Ftlink li{
  margin: 1rem 2rem;
}

.pg--Ftlink li a{
  color: #fffa;
  letter-spacing: 2px;
  text-transform: uppercase;

}