.hero--bg{
    height: 85vh;
    background-image:linear-gradient(rgba(0,0,0,.7), rgba(0,0,0,.7)), url('https://picsum.photos/1920/1080') ;
    padding: 100px 0 50px;
}

:focus-visible{
    outline: none;
}

.hero--slider .slick-prev, 
.hero--slider .slick-next{
    position: absolute;
    right: 10% !important;
    top : 100px !important; 
    left: unset !important ;
    transform: rotate(90deg) ; 
    z-index: 2;
    opacity: 1 !important;
}

.hero--slider .slick-next{
    top:  150px !important;
}

.hero--slider .slick-prev:before{
    content: '';
    width:15px;
    height: 15px;
    display: block;
    border-left: 2px solid #fff;
    border-bottom: 2px solid #fff;
    transform: rotate(45deg);
}

.hero--slider .slick-next:before{
    content: '';
    width:15px;
    height: 15px;
    display: block;
    border-right: 2px solid #fff;
    border-top: 2px solid #fff;
    transform: rotate(45deg);
}

.hero--bg .container,
.hero--bg .row{
    height: 100%;
}

.appointment--form{
    display: flex;
    border-radius: 15px;
    overflow: hidden;
    justify-content: space-between;
    padding-right: 0;
    background-color: #fff;
    box-shadow: 0 0 10px #0005;
}
.appointment--form > div{
    padding: 2rem;
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.appointment--form input:focus,
.appointment--form select:focus,
.appointment--form button:focus{
    outline: none;    
    box-shadow: none;
}

.appointment--form input,
.appointment--form select{
    border: none;
    margin-right: 10px;
    font-weight: 500;
}
.appointment--form input,
.appointment--form select,
.appointment--form label{
    padding-left: 1rem;
}


.appointment--form button{
    padding: 2rem;
    border: none;
    background-color: orangered;
    color: #fff;
    white-space: nowrap;
    font-weight: bold;
}


.hero--bg h4{
    font-size: 48px;
    line-height: 1;
    font-weight: bold;
    padding-top: 1.5rem;
    margin-bottom: 16px;
    color: #fff;
    position: relative;
}
.hero--bg h4::before{
    content: '';
    position: absolute;
    width: 100px;
    height: 5px;
    background-color:#fff;
    top: 0;
    left: 0;
}

.hero--bg p{
    font-size: 14px;
    line-height: 1.75;
    color: #fffa;
}





.prgr--services{
    padding: 100px 0;
}

.prgr-section-title{
    font-size: 42px;
    text-transform: capitalize;
    font-weight: 300;
}


.srv--list{
    list-style: none;
    padding-left: 0;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-end;
}

.srv--list li > a{
    display: block;
    width: 250px;
    height: 250px;
    text-align: center;
    padding: 2rem;
    border-radius: 10px;
    color: #000;
    text-decoration: none;
    font-weight: 500;
}

.srv--list li:nth-child(even)  a{
    background-color: #0002;
}

.srv--list a img{
    width: 100px;
    object-fit: contain;
    display: block;
    margin: 2rem auto ;
}

.prgr--services .prgr-read-btn{
    width: 50px;
    height: 50px;
    background-color: #000;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-weight: bold;
    font-size: 24px;
}







.prgr-news{
    padding: 100px 0;
    position: relative;
    min-height: 100vh;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.prgr-news h4{
    border-bottom: 3px solid ;
    padding-bottom: 1rem;
    margin-bottom: 2rem;
    width: 300px;
}


.prgr-news-title{
    font-size: 42px;
    font-weight: bold;
    color: #fff;
    margin-bottom: 2rem;
}

.prgr-news-link{
    position: relative;
    text-decoration: none;
    color: #Fff;
}

.prgr-news-link i{
    border-radius: 5px;
    border: 1px solid;
    width: 40px;
    height: 40px;
    margin-right: 5px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
}


.prgr-news-thumnail{
    margin-top: 6rem;
    display: flex;
    justify-content: space-between;
    flex-basis: 320px;
    color: #fff;
    padding: 0 .75rem;
}

.news-video-slide{
    width: 300px;
    height: 4px;
    border-radius: 2px;
    display: block;
    background-color: #fff5;
    margin: 1rem 0;
}











.prgr-works{
    padding :100px 0;
}

.work--container{
    position: relative;;
    max-width: 320px;
    width: 100%;
    height: 320px;
    margin: 0px 100px;
    border-radius: 4px;
    overflow: hidden;
}

.work--container > *{
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.content-overlay{
    opacity: 0;
    transition: .5s ease;
    background-color: #000a;
    padding: 1rem;
    color: #fff;
}

.content-overlay h4{
    font-size: 28px;
    line-height: 1;
    width: 70%;
}

.content-overlay i{
    color: #fffc;
}

.content-overlay .work--brand{
    position: absolute;
    color: #fffc;
    left: 1rem;
    bottom: 1rem;

}

.work--link{
    position: absolute;
    right: 0;
    bottom: 0;
    width: 50px;
    height: 50px;
    font-size: 1.5rem;
    background-color: orangered;
    color: #fff;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
}

.work--container:hover .content-overlay{
    opacity: 1;
}

.work--slider{
    padding-bottom: 1.5rem;
}

.work--slider .slick-dots li.slick-active button:before{
    background-color: orangered;
}
.work--slider .slick-dots li{
    margin: 0 10px;
    width: 50px;
    height: 4px;
}
.work--slider .slick-dots li button:before{
    content: '';
    width: 100%;
    height: 100%;
    background-color: #000a;
    border-radius: 10px;
}


.work--slider .slick-prev,
.work--slider .slick-next{
    top: -100px;
    left: 70%;
    background-color: transparent;
    width: 30px;
    height: 30px;
    display: block;
    background-color: #0002;
    
    border-radius: 5px;
}

.work--slider .slick-next{
    left: calc(70% + 40px);
}

.work--slider .slick-prev:before{
    content: '';
    position: absolute;
    left: 35%;
    top : 60%;
    display: block;
    width: 10px;
    height: 10px;
    border-bottom: 2px solid orangered;
    border-left: 2px solid orangered;
    transform: rotate(45deg) translate(-50%,-50%);
}

.work--slider .slick-next:before{
    content: '';
    display: block;
    position: absolute;
    left: 50%;
    top : 35%;
    width: 10px;
    height: 10px;
    border-bottom: 2px solid orangered;
    border-right: 2px solid orangered;
    transform: rotate(-45deg) translate(-50%,-50%);
}














